.Service .Wavyrow {
    position: relative;
    background: #bdc3c7;
    background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
    background: linear-gradient(to right, #2c3e50, #bdc3c7);
    width: 100%;
    height: 28vh;
    margin: 0px;
    margin-bottom: -10%;
  }
 
  .Service .Wavyrow svg {
    position: absolute;
    bottom: 0;
    padding: 0;
  }
 
  .Service .cards {
    max-width: 1200px;
    margin: 10% 10% 5%;
    display: grid;
    gap: 1rem;
  }
 
  .Service .card {
    border-color: transparent;
    padding: 2% 0;
    background-color: transparent;
  }
 
  .Service .Texte {
    padding: 0 10%;
    text-align: start;
  }
 
  .Service img {
    width: 90%;
    align-items: center;
    margin-top: 2%;
  }
 
  .Service li {
    list-style: none;
    margin-bottom: 10px;
  }
 
  .Service a {
    color: black;
    text-decoration-color: none;
 
  }
 
  .Service .back {
    margin-bottom: 5%;
    text-align: left;
    padding: 0 0 0 10%;
  }
 
  .Service .pic {
   width: 50%;
   bottom: -10%;
   left: 55%;
   height: 60%;
   position: absolute;
   border: 10px solid #ffffff;
   /* Add your border style */
  }
 
  /* larger than 900px make it 2 columns */
  @media (min-width: 951px) {
 
    .Service .cards,
    .cardsA1 {
      grid-template-columns: repeat(2, 1fr);
    }
  }
 
  /* larger than 900px make it 2 columns */
  @media (max-width: 950px) {
 
  
    .Service .Wavyrow {
      height: 15vh;
      margin: 0px;
      border-bottom: 0.01px solid white;
      /* Choisissez l'épaisseur de la ligne que vous souhaitez */
 
    }
 
 
    .Service .Wavyrow svg {
      position: absolute;
      bottom: 0;
      padding: 0;
    }
    
    .Service .Texte {
      padding: 0 ;
     }
  }
 
