 .contact {
   /* the container  */
   padding: 0;
   justify-content: flex;
   justify-content: center;
   align-items: center;
   overflow: hidden;
  }

 .contact .Wavyrow {
   position: relative;
   background: #bdc3c7;
    background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
    background: linear-gradient(to right, #2c3e50, #bdc3c7);
   width: 100%;
   height: 28vh;
   margin: 0;
 }

 .contact .Wavyrow svg {
   position: absolute;
   bottom: 0;
   padding: 0;
 }

 .rowC {
   padding: 0 15% 5%;
 }

 .contact .rowC .leftcol {
   /* the left col align items */
   text-align: start;
  }

 .contact .GrandTitre {
   /*****Titre en Grand ***/
   text-align: start;
   text-decoration-line: underline 1px;
   text-decoration-color: #2c3e50;
  

 }

 .contact .zoneTexteArea {
   /****TextArea Formulaire style***/
   border-radius: 2px;
   padding: 10px 25px;
 }
 .contact .motif{
  border: 1px solid #d5d5d5bd; /* Add your border style */
   
 }
 .contact .groupChoiceMotif {
   /***** Card which contains RadiosButton ***/
   margin-top: 1vh;
   padding: 10px;
   border-radius: 1px;
   

 }

 .contact .input-group-text {
   /***** Card which contains RadiosButton to check ***/
   border-color: rgb(255, 255, 255);
   background-color: rgb(255, 255, 255);
 }

 .contact .check {
   cursor: pointer;
 }

 .contact #w3review {
   /****Définir vos besoin***/
   margin-top: 1vh;
   padding: 10px;
   border-radius: 2px;
   border-color: #dddddd;
   width: 100%;
   resize: vertical;
   max-width: 800px;
 }

 /*****typeFichier***/
 .contact .typeFichier {
   margin-top: 1vh;
   padding: 10px;
   border-radius: 2px;
   border-color: #d8d7d7;
   background-color: rgb(221, 220, 220);
   font-style: italic;

 }

 /*******JACCEPTE LES CONDITIONS*****/
 .contact .jacceptezone {
   background-color: rgb(255, 255, 255);
   box-shadow: 0 0 1px #3b3b3b;
   padding: 10px;
   border-radius: 2px;
   border-color: #000000;
 }

 .contact .checkzone {
   padding: 1px;
 }

 .contact .jaccepte {
   cursor: pointer;
 }

 /******BOUTTON JE DEMANDE DEVIs*****/
 .contact .devis {
   background-color: #2c3e50;
   width: 200px;
   height: 40px;
   padding: 10px 20px;
   color: rgb(255, 255, 255);
   border-radius: 1px;

 }
.contact .contain-button{
  text-align: start;

}
 .contact .devis:hover {
   background-color:#2c3e50;

 }

 .contact .annuler {
   background-color: rgb(255, 255, 255);
   border: 1px solid #000000bd; /* Add your border style */
   width: 200px;
   height: 40px;
   padding: 10px 20px;
   color: rgb(0, 0, 0);
   border-radius: 1px;
   margin: 4%;
 }
 
 .contact .annuler:hover {
  background-color: rgb(255, 255, 255);

 }

 /********* ROW RIGHT  ROW RIGHT ROW RIGHT ROW RIGHT  ROW RIGHT ROW RIGHT ROW RIGHT*********/
 .contact .rightcol {
   /* right col padding */
   padding: 60px 10px;
  }

 .contact .info {
   text-align: start;
 }

 .contact a {
   color: rgb(0, 0, 0);
   text-decoration: none;
 }
 

 @media screen and (max-width:900px) {
   
  .contact .rowC {
    padding: 5%; 
  display: grid;
  grid-template-columns: repeat(1, 1fr); /* One column */
  grid-template-rows: auto auto;
}
.contact .rightcol {
  /* right col padding */
  }
   .contact .devis , .contact .annuler {
     width:200px ;
     height: 40px;
    margin-left: 15%    }
   
 
 .contact .zoneTexteArea {
  /****TextArea Formulaire style***/
  border-radius: 2px;
  padding: 5px 10px;
}
.contact .Wavyrow {
  height: 15vh;
  margin: 0px;
}
 
 }


 