
.expertises .Wavyrow {
  position: relative;
  background: #bdc3c7;
    background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
    background: linear-gradient(to right, #2c3e50, #bdc3c7);
  width: 100%;
  height: 28vh;
  margin: 0px;
  margin-bottom: -10%;
  overflow: hidden;
}

.expertises .Wavyrow svg {
  position: absolute;
  bottom: 0;
  padding: 0;
}
.expertises {
  /* the fluid container */
  justify-content: flex;
  justify-content: center;
  align-items: center;
  padding: 0%;
}
.expertises .Titre{
  overflow: hidden;
  text-decoration: underline 1px;
  text-decoration-color: #2c3e50;
  text-align: start;
  margin: 10% 5% -2% 10%;

}
.expertises .cardsE {/* the card's container*/
  margin: 0  10% 10% ;
  display: grid;
  }
.expertises .card {
  /* the card's style */
  width: 90%;
  height: auto;
  padding: 0px ;
  background-color: rgba(163, 173, 180, 0.132);
  margin-top: 10%;
  margin-bottom: 10%;
  border: none;
  border-radius: 0px;
 }
.expertises  img:hover{
  transform: scale(1.1);
  }
.expertises img {
  width: 98%;
  height: 50%;
  object-fit: cover;
  margin: 2%;
  transition: all 0.3s;


  }

.expertises a {
  color: black;
}

.expertises .card-body h5, p {
text-align:  start;
justify-content: start;
} 
.expertises .card-body h5 {

  
    font-family: 'Outfit', sans-serif;
   
     
} 



.expertises .card-body  {
  height: 30vh;
 } 
/* larger than 1251px make it 2 columns */
@media (min-width: 1251px) {
  .cardsE {
    grid-template-columns: repeat(3, 1fr);
  }
}


/* less than 1250px make it 2 columns */
@media (max-width:1250px) {
  .cardsE {
    grid-template-columns: repeat(2, 1fr);
  }
  .expertises .card {
    /* the card's style */
     height: 550px;
      
   }
}


/* less than 700px make it 1 columns */
@media (max-width:700px) {
  .cardsE {
    margin-top: 25%;
     grid-template-columns: repeat(1, 1fr);
 
  }
  .expertises .card {
    /* the card's style */
     width: 300px;
       
   }

  .expertises .Wavyrow {
    height: 15vh;
    margin: 0px;
   }

  .expertises .Wavyrow svg {
    position: absolute;
    bottom: 0;
    padding: 0;
  }
}


