 .home {
   padding:  0%;
   height: auto;
     /* the container  */
    padding: 0;
    justify-content: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }


 .home .Wavyrow {
   position: relative;
   background: #bdc3c7;
   background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
   background: linear-gradient(to right, #2c3e50, #bdc3c7);
   width: 100%;
   height: 28vh;
   margin: 0px;
   margin-bottom: -10%;
   border-bottom: 0.01px solid white;
   /* Choisissez l'épaisseur de la ligne que vous souhaitez */


 }

 .home .Wavyrow svg {
   position: absolute;
   bottom: 0;
   padding: 0;
 }

 .home .CardsContainer {
   /***container cards**/
   height: auto;
   display: grid;
   gap: 1rem;
   margin-top: 10%;
   border-color: transparent;
   margin-bottom: 10%;
 
 }



 .home .CardsContainer .CARD {
   /*    animation: 10s infinite anim;*/
   border-color: transparent;
   background: linear-gradient(to bottom, #bdc3c739, transparent);
   border-radius: 80% 80% 70% 10%;
   max-width: 1300px;
   margin-top: -5%;
 }

 .home img {
   display: block;
   width: 100%;
   height: auto;
   margin-left: 10%;
 }

 @keyframes anim {
   0% {
     transform: rotateY(0);
   } 

   50% {
     transform: rotateY(180deg);
   }
 }

 /***********/

 .home .CARDT {
   border-color: transparent;
   text-align: center;
   margin: 5%;
   margin-right: 15%;
  }

 .home .CARDT p {
   text-align: center;
  }

 .home .CARDT .btn-grad   {
   padding: 2% 4%;
   text-transform: uppercase;
   background-size: 200% auto;
   margin-top: 5%;
   background-color: #2c3e50;
   border: none;
   width: 200px;
 }

 .home .CARDT a .btn-grad  {
   text-decoration: none;
   color: rgb(255, 255, 255);

 }

 .home .CARDT a .btn-grad a:hover {
   text-decoration: none;
   color: white;
 }

 /* larger than 900px make it 2 columns */
 @media (min-width: 950px) {
   .CardsContainer {
     grid-template-columns: repeat(2, 1fr);
   }
 }

 /* less than 951px make it 2 columns */
 @media (max-width: 951px) {
   .CardsContainer {
     margin-top: 15%;
   }

   .home .Wavyrow {
     height: 15vh;
     margin: 0px;
     border-bottom: 0.01px solid white;
     /* Choisissez l'épaisseur de la ligne que vous souhaitez */

   }


   .home .Wavyrow svg {
     position: absolute;
     bottom: 0;
     padding: 0;
   }

 }

  