.About {
  /* the container  */
  padding: 0;
  justify-content: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
 
}

 .About .Wavyrow {
   position: relative;
   background: #bdc3c7;
   background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
   background: linear-gradient(to right, #2c3e50, #bdc3c7);
   width: 100%;
   height: 28vh;
   margin: 0px;
   margin-bottom: -10%;
 }

 .About .Wavyrow svg {
   position: absolute;
   bottom: 0;
   padding: 0;
 }

 .About .cardsA {
   max-width: 1300px;
   margin: 0 auto;
   display: grid;
   gap: 1rem;
   padding: 5%;
 }

 .About .card {
   border-color: transparent;
   background-color: transparent;
 }

 .About .Texte {
   padding: 0 10%;
   text-align: start;
 }

 .About img {
   width: 100%;
   align-items: center;
 }

 .About .titre {
   margin-top: 10%;
   text-align: start;
   margin-left: 12%;
   color: #34495e; /* A darker gray for the light portion */

   text-decoration: underline 1px;
   text-decoration-color: #2c3e50;
 }

 /* larger than 900px make it 2 columns */
 @media (min-width: 951px) {

   .About .cardsA,
   .cardsA1 {
     grid-template-columns: repeat(2, 1fr);
   }
 }

 /* larger than 900px make it 2 columns */
 @media (max-width: 950px) {
 
   .About .Wavyrow {
    height: 15vh;
    margin: 0px;
    border-bottom: 0.01px solid white;
    /* Choisissez l'épaisseur de la ligne que vous souhaitez */

  }


  .About .Wavyrow svg {
    position: absolute;
    bottom: 0;
    padding: 0;
  }
  
  .About .Texte {
    padding: 0 ;
   }
 }

 /********************************************************/

 .About .cardsA1 {
   max-width: 1300px;
   margin: 10% auto;
   display: grid;
   gap: 1rem;
   padding: 5%;
   background: #2c3e50;
 }

 .About .cardsA1 .Texte {
   color: white;

 }