 .Conception .Wavyrow {
   position: relative;
   background: #bdc3c7;
   background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
   background: linear-gradient(to right, #2c3e50, #bdc3c7);
   width: 100%;
   height: 28vh;
   margin: 0px;
   margin-bottom: -10%;
 }

 .Conception .Wavyrow svg {
   position: absolute;
   bottom: 0;
   padding: 0;
 }
 
 .Conception .cards {
  max-width: 1200px;
  margin: 10% 10% 5%;
  display: grid;
  gap: 1rem;
 }

 .Conception .card {
   border-color: transparent;
    background-color: transparent;
 }

 .Conception .Texte {
   padding: 0 0 0 8%;
   text-align: start;
 }

 .Conception img {
   width: 100%;
   align-items: center;
   margin-top: 2%;
 }

 .Conception li {
   list-style: none;
   margin-bottom: 10px;
 }

 .Conception a {
   color: black;
   text-decoration-color: none;

 }

 .Conception .back {
   margin-bottom: 5%;
   text-align: left;
   padding: 0 0 0 10%;
 }

 .Conception .pic {
  width: 55%;
  bottom: -10%;
  left: 55%;
  height: 65%;
  position: absolute;
  border: 10px solid #ffffff;
  /* Add your border style */
 }

 /* larger than 900px make it 2 columns */
 @media (min-width: 951px) {

   .Conception .cards,
   .cardsA1 {
     grid-template-columns: repeat(2, 1fr);
   }
 }

 /* larger than 900px make it 2 columns */
 @media (max-width: 950px) {

   .Conception .cards,
   .cardsA1 { 
     margin-top: 20%;
   }
   .Conception .Texte {
    padding: 0 ;
   }
   .Conception .Wavyrow {
    height: 15vh;
    margin: 0px;
    border-bottom: 0.01px solid white;
    /* Choisissez l'épaisseur de la ligne que vous souhaitez */

  }


  .Conception .Wavyrow svg {
    position: absolute;
    bottom: 0;
    padding: 0;
  }
 
 }

