.sponsor {
  /* the fluid container */
  justify-content: flex;
  justify-content: center;
  align-items: center;
  padding: 0%;
}

.sponsor .Wavyrow {
  position: relative;
  background: #bdc3c7;
  background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
  background: linear-gradient(to right, #2c3e50, #bdc3c7);
  width: 100%;
  height: 28vh;
  margin: 0px;
  margin-bottom: -10%;
}

.sponsor .Wavyrow svg {
  position: absolute;
  bottom: 0;
  padding: 0;
}

.sponsor .container {
  margin-top: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 40px;

}

.sponsor .card {
  position: relative;
  width: 250px;
  height: 450px;
  margin: 30px;
  border-radius: 20px;
  border-top-left-radius: 160px;
  border-top-right-radius: 160px;
  box-shadow: 0 15px 0 #fff, inset 0 15px 0 #2c3e5044;
  border: none;
  background-color: rgba(163, 173, 180, 0.132);
}

.sponsor .card-body {
  margin-top: 0;
  list-style: none;
  margin-top: 20%;
  color: rgb(0, 0, 0);

}

.sponsor .card-body .titre {
  font-family: 'Oswald', sans-serif;
  background-color: rgba(255, 255, 255, 0.25);
  margin: 0;
  border-top-left-radius: 160px;
  border-top-right-radius: 160px;
  color: rgb(0, 0, 0);


}

.sponsor a {
  color: black;
  text-decoration-color: none;

}

.sponsor .back {
  margin-bottom: 5%;
  text-align: left;
  padding: 0 0 0 20%;


}

.sponsor .pack {
  padding: 1%;
  text-transform: uppercase;
  background-size: 100% auto;
  margin-bottom: 5%;
  background-color: #2c3e50;
  border: none;
  width: 400px;
}

.sponsor .pack a {
  text-decoration: none;
  color: rgb(255, 255, 255);

}

.sponsor .pack a:hover {
  text-decoration: none;
  color: white;
}

/* less than 900px make it 1 columns */
@media (min-width: 901px) {

  .sponsor .card {
    max-width: 250px;
    height: 450px;
  }

}

/* less than 900px make it 1 columns */
@media (max-width: 900px) {

  .sponsor .Wavyrow {
    height: 15vh;
    margin: 0px;
    border-bottom: 0.01px solid white;
    /* Choisissez l'épaisseur de la ligne que vous souhaitez */

  }


  .sponsor .Wavyrow svg {
    position: absolute;
    bottom: 0;
    padding: 0;
  }

  .sponsor .container {
    margin-top: 0;

  }
}