 
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap');
 
nav {
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    width: 100%;
    height: 0;
    position: relative;
    top: 25px;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
     font-size: 18px;
}

nav .logo {
    width: 100px;
    height: 50px;
 }

#navbar {
    display: flex;
    justify-content: center;
    align-items: center;
}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a {
    text-decoration: none;
    font-weight: 600;
    color: rgb(0, 0, 0);
    transition: 0.3s ease-in-out;
}

#navbar li a:hover {

    color: white;
}

#navbar li a:hover::after {
    content: '';
    width: 30%;
    height: 2px;
    background: white;
    position: absolute;
    bottom: -4px;
    left: 20px;
}


/************* M O B I L E *****************/
#mobile {
    display: none;
}

#mobile i {
    color: rgb(0, 0, 0);
    align-items: center;
}

@media screen and (max-width:769px) {
    #navbar {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        position: fixed;
        top: 70px;
        right: -450px;
        width: 300px;
        height: 100vh;
        background-color: rgb(255, 255, 255);
        box-shadow: 0 40px 10px rgba(0, 0, 0, 0.1);
        padding: 40px 0 0 10px;
        transition: 0.3s ease-in-out;

    }

    #navbar.active {
        right: 0;
         padding: 45% 5% 0 0;
    }

    #navbar li {
        margin-bottom: 35px;
          
    }

    #mobile {
        display: block;
    }

    #mobile i {
        font-size: 24px;
        cursor: pointer;
    }

}