 .Reseaux .Wavyrow {
   position: relative;
   background: #bdc3c7;
   background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
   background: linear-gradient(to right, #2c3e50, #bdc3c7);
   width: 100%;
   height: 28vh;
   margin: 0px;
   margin-bottom: -10%;
 }

 .Reseaux .Wavyrow svg {
   position: absolute;
   bottom: 0;
   padding: 0;
 }

 .Reseaux .cards {
   max-width: 1200px;
   margin: 10% 10% 5%;
   display: grid;
   gap: 1rem;
 }

 .Reseaux .card {
   border-color: transparent;
   padding: 2% 0;
  }

 .Reseaux .Texte {
   padding: 0 10%;
   text-align: start;
 }

 .Reseaux img {
   width: 100%;
   align-items: center;
   margin-top: 2%;
 }

 .Reseaux li {
   list-style: none;
   margin-bottom: 10px;
 }
.Reseaux a{
  color: black;
  text-decoration-color: none;

}

.Reseaux .back{
  margin-bottom: 5%;  
  text-align: left;
  padding:0 0 0 10% ;
}
 /* larger than 900px make it 2 columns */
 @media (min-width: 950px) {

   .Reseaux .cards  {
     grid-template-columns: repeat(2, 1fr);
   }
 }

 /* larger than 900px make it 2 columns */
 @media (max-width: 951px) {

   .Reseaux .cards {
     margin-top: 20%;
   }
   .Reseaux .Texte {
    padding: 0 ;
    text-align: start;
  }
 }

